.link {
  height: 89vh;
  background-size: cover;
  display: grid;
  grid-template-columns: repeat(5, 20%);
  align-items: center;
}

.usefull-link {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translate(-50%, 0);
  font-size: 56px;
  text-decoration: underline;
}

.link-card {
  background-color: rgba(255,255,255, .4);
  margin: auto;
  width: 204px;
  height: 270px;
}

.link-title {
  font-size: 80px;
  font-weight: lighter;
}

.link-btn {
  text-decoration: none;
  color: black;
  transition: ease .3s;
  &:hover {
    text-decoration: none;
    color: black;
    transform: scale(1.2);
  }
}

.flex-link {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100%;
  width: 100%;
  padding: 40px 0;
}
